<template>
  <div class="home1">
    <my-swiper></my-swiper>
    <sub-nav></sub-nav>
    <ul class="classify">
      <li class="classify-item">
        <p>
          <img src="../../common/images/bag.png" alt>
        </p>
        <p class="name">包包养护</p>
      </li>
      <li class="classify-item">
        <p>
          <img src="../../common/images/bag.png" alt>
        </p>
        <p class="name">包包养护</p>
      </li>
      <li class="classify-item">
        <p>
          <img src="../../common/images/bag.png" alt>
        </p>
        <p class="name">包包养护</p>
      </li>
      <li class="classify-item">
        <p>
          <img src="../../common/images/bag.png" alt>
        </p>
        <p class="name">包包养护</p>
      </li>
    </ul>

    <div class="maintenance-case">
      <h2 class="title">养护案例</h2>
      <van-tabs v-model="active" class="tab-nav" animated swipeable>
        <van-tab v-for="(item,index) in lists" :key="index">
          <div slot="title">
            <div class="tab-title">
              <p>{{item.name}}</p>
              <p>{{item.enName}}</p>
            </div>
          </div>
          <img :src="item.img" alt="商品" class="good-img">
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import MySwiper from "components/my-swiper/my-swiper";
import SubNav from "components/sub-nav/sub-nav";
import AlertModel from "components/alert-model/alert-model";

export default {
  data() {
    return {
      active: 0,
      sendVal: false,
      lists: [
        {
          name: "包包",
          enName: "Bags",
          img: "http://localhost:6961/Images/NewWXShop/curingCase_bag.png"
        },
        {
          name: "鞋靴",
          enName: "Shoes",
          img: "http://localhost:6961/Images/NewWXShop/curingCase_shoes.png"
        },
        {
          name: "服饰",
          enName: "Appeal",
          img: "http://localhost:6961/Images/NewWXShop/curingCase_coat.png"
        },
        {
          name: "配饰",
          enName: "Accessories",
          img: "http://localhost:6961/Images/NewWXShop/curingCase_ornament.png"
        }
      ]
    };
  },
  methods: {
    openMask(index) {
      this.sendVal = true;
    },
    clickCancel() {
      console.log("点击了取消");
    },
    clickDanger() {
      console.log("这里是danger回调");
    },
    clickConfirm() {
      console.log("点击了confirm");
    }
  },
  components: {
    MySwiper,
    SubNav,
    AlertModel
  }
};
</script>

<style lang="less" scoped>
.tab-nav /deep/ .van-tabs__wrap {
  height: 100px !important;
  margin: 20px 0 30px;
  z-index: 0;
}
.tab-nav /deep/ .van-tabs__content {
  padding: 120px 10px 20px;
  text-align: center;

  .good-img {
    width: 90%;
  }
}

.home1 {
  .classify {
    width: 100%;
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    background: #fff;
    font-size: 30px;
    border-top: 14px solid #efeff4;
    .classify-item {
      width: 50%;
      text-align: center;
      padding: 20px 0;
      img {
        width: 222px;
      }
      .name {
        padding: 10px 0;
      }
    }
  }
  .maintenance-case {
    padding: 30px 50px;
    background: #fff;
    .title {
      font-size: 32px;
      font-weight: 400;
    }
    .tab-nav {
      width: 100%;
      padding: 30px 0;
      // & /deep/ .van-tabs__wrap {
      //   height: 100px !important;
      //   margin: 20px 0 30px;
      //   z-index: 0;
      // }
      // & /deep/ .van-tabs__content {
      //   padding: 120px 10px 20px;
      //   text-align: center;

      //   .good-img {
      //     width: 90%;
      //   }
      // }
      .tab-title {
        font-size: 28px;
      }
    }
  }
  .open-model {
    margin-bottom: 90px;
    height: 50px;
  }
}
</style>




